<template>
    <section class="section">
        <div class="card">
            <div class="card-header">
                <p class="title is-3">{{ name }}</p>
            </div>
            <div class="card-content">
                <div class="level is-mobile">
                    <div class="level-item">
                        <div class="has-text-centered">
                            <p class="title is-4">Sex</p>
                            <p>{{ session.user.sex }}</p>
                        </div>
                    </div>
                    <div class="level-item">
                        <div class="has-text-centered">
                            <p class="title is-4">Age</p>
                            <p>{{ age }}</p>
                        </div>
                    </div>
                </div>
                <div class="level is-mobile">
                    <div class="level-item">
                        <div class="has-text-centered">
                            <p class="title is-4">Height</p>
                            <p>{{ heightString }}</p>
                            <a
                                class="card-footer-item"
                                @click="modalHeight = true"
                                >Update
                            </a>
                        </div>
                    </div>
                    <div class="level-item">
                        <div class="has-text-centered">
                            <p class="title is-4">Weight</p>
                            <p>{{ weight }} lbs.</p>
                            <a
                                class="card-footer-item"
                                @click="modalWeight = true"
                                >Update
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal" :class="{ 'is-active': modalHeight }">
            <div class="modal-card">
                <header class="modal-card-head">
                    <p class="modal-card-title">Update Height</p>
                </header>
                <section class="modal-card-body">
                    <input
                        class="input"
                        type="number"
                        v-model="newHeight"
                        placeholder="new height in inches"
                    />in.
                </section>
                <footer class="modal-card-foot">
                    <button class="button is-success" @click="submitHeight()">
                        Update
                    </button>
                    <button class="button" @click="modalHeight = false">
                        Cancel
                    </button>
                </footer>
            </div>
        </div>

        <div
            id="weightModal"
            class="modal"
            :class="{ 'is-active': modalWeight }"
        >
            <div class="modal-card">
                <header class="modal-card-head">
                    <p class="modal-card-title">Update Weight</p>
                </header>
                <section class="modal-card-body">
                    <input
                        class="input"
                        type="number"
                        v-model="newWeight"
                        placeholder="new weight in pounds"
                    />lbs.
                </section>
                <footer class="modal-card-foot">
                    <button class="button is-success" @click="submitWeight()">
                        Update
                    </button>
                    <button class="button" @click="modalWeight = false">
                        Cancel
                    </button>
                </footer>
            </div>
        </div>
    </section>
</template>

<script>
import session from "../services/session";

export default {
    name: "MyProfile",
    data: () => ({
        session,
        height: 68,
        weight: 150,
        modalHeight: false,
        modalWeight: false,
        newHeight: null,
        newWeight: null,
    }),
    methods: {
        submitHeight() {
            this.height = this.newHeight;
            this.modalHeight = false;
        },
        submitWeight() {
            this.weight = this.newWeight;
            this.modalWeight = false;
        },
    },
    computed: {
        name() {
            return (
                this.session.user.firstName + " " + this.session.user.lastName
            );
        },
        age() {
            let today = new Date();
            let bday = new Date(this.session.user.birthday);
            let age = today.getFullYear() - bday.getFullYear();
            let m = today.getMonth() - bday.getMonth();
            if (m < 0 || (m === 0 && today.getDate() < bday.getDate())) {
                age--;
            }
            return age;
        },
        heightString() {
            return Math.floor(this.height / 12) + "' " + (this.height % 12) + '"';
        }
    },
};
</script>

